.table {
	display: table;
}
.fixed {
	table-layout: fixed;
}
.row {
	display: table-row;
}
.cell {
	display: table-cell;
}
.cell-align {
	vertical-align: middle;
}
.cell-1 {
	width: 8.333%;
}
.cell-2 {
	width: 16.666%;
}
.cell-3 {
	width: 25%;
}
.cell-4 {
	width: 33.333%;
}
.cell-5 {
	width: 41.666%;
}
.cell-6 {
	width: 50%;
}
.cell-7 {
	width: 58.333%;
}
.cell-8 {
	width: 66.666%;
}
.cell-9 {
	width: 75%;
}
.cell-10 {
	width: 83.333%;
}
.cell-11 {
	width: 91.666%;
}
.cell-12 {
	width: 100%;
}